import React,{Component} from 'react';
import './index.less'
export default class LoginComponent extends Component{
    constructor(){
        super();
        this.state = {
            val:''
        }
    }
    render(){
        return (
            <div className="login-component">
                {/*ref   onChange + state */}
                <input
                    type="text"
                    value={this.state.val}
                    onChange={this.changeValue.bind(this)}
                    placeholder=""
                />
                <button onClick={this.login.bind(this)} className="back"></button>
            </div>
        )
    }
    changeValue(e){
        this.setState({
            val:e.target.value
        });
    }
    login(){
        //
        this.props.login(this.state.val);
    }

}


