import * as Types from '../action-types/store';
let initState = [];
export function store(state=initState,action){
    switch (action.type){
        case Types.ADD_STORE:
            //id,
            return [...state,action.data];
        case Types.REMOVE_STORE:
            //id
            return state.filter(item=>action.data!==item);
        default:
            return state;
    }
}


